export const dynamic = 'force-dynamic';

import Link from 'next/link';
import { Plus, Edit } from 'lucide-react';
import Experience from '@/models/Experience';
import connectToDatabase from '@/lib/db';
import ExperienceDeleteButton from '@/components/admin/ExperienceDeleteButton';

async function getExperience() {
    await connectToDatabase();
    const experience = await Experience.find({}).sort({ startDate: -1 });
    return experience;
}

export default async function AdminExperience() {
    const experience = await getExperience();

    return (
        <div>
            <div className="flex justify-between items-center mb-8">
                <h1 className="text-3xl font-display text-primary">Experience</h1>
                <Link href="/admin/experience/new" className="flex items-center gap-2 bg-white text-black px-4 py-2 rounded font-semibold hover:bg-gray-200 transition-colors">
                    <Plus size={18} /> Add Experience
                </Link>
            </div>

            <div className="bg-surface border border-border rounded-lg overflow-hidden">
                <table className="w-full text-left">
                    <thead className="bg-surfaceHighlight">
                        <tr>
                            <th className="p-4 text-sm font-medium text-secondary">Company</th>
                            <th className="p-4 text-sm font-medium text-secondary">Position</th>
                            <th className="p-4 text-sm font-medium text-secondary">Duration</th>
                            <th className="p-4 text-sm font-medium text-secondary text-right">Actions</th>
                        </tr>
                    </thead>
                    <tbody className="divide-y divide-border">
                        {experience.map((exp: any) => (
                            <tr key={exp._id} className="hover:bg-surfaceHighlight/50 transition-colors">
                                <td className="p-4 text-primary font-medium">{exp.company}</td>
                                <td className="p-4 text-secondary">{exp.position}</td>
                                <td className="p-4 text-secondary text-sm">
                                    {new Date(exp.startDate).toLocaleDateString(undefined, { month: 'short', year: 'numeric' })}
                                    {' - '}
                                    {exp.current ? 'Present' : new Date(exp.endDate).toLocaleDateString(undefined, { month: 'short', year: 'numeric' })}
                                </td>
                                <td className="p-4 text-right flex justify-end gap-3 items-center">
                                    <Link href={`/admin/experience/${exp._id}`} className="text-accent hover:text-white transition-colors">
                                        <Edit size={18} />
                                    </Link>
                                    <ExperienceDeleteButton id={exp._id.toString()} />
                                </td>
                            </tr>
                        ))}
                        {experience.length === 0 && (
                            <tr>
                                <td colSpan={4} className="p-8 text-center text-secondary">No experience entries found.</td>
                            </tr>
                        )}
                    </tbody>
                </table>
            </div>
        </div>
    );
}
